* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

body {
  font-family: var(--userfont);
  font-size: var(--regular-text);
  line-height: var(--lineheight);
  background: #2f363e;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.container {
  background: #2f363e;
  border-radius: 225px 225px 20px 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 25px 25px 75px #000000bf, 10px 10px 70px #00000040, inset 5px 5px 10px #00000080, inset 5px 5px 20px #fff3, inset -5px -5px 15px #000000bf;
}

.clock {
  background: #2f363e;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 450px;
  height: 450px;
  margin-bottom: 30px;
  display: flex;
  position: relative;
  box-shadow: 10px 50px 70px #00000040, inset 5px 5px 10px #00000080, inset 5px 5px 20px #fff3, inset -5px -5px 15px #000000bf;
}

.clock:before {
  content: "";
  z-index: 1000;
  background: #2f363e;
  border: 3px solid #fff;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  position: absolute;
}

.clock span {
  color: #fff;
  text-align: center;
  transform: rotate(calc(30deg * var(--i)));
  position: absolute;
  inset: 20px;
}

.clock span b {
  opacity: .25;
  transform: rotate(calc(-30deg * var(--i)));
  font-size: 2em;
  font-weight: 600;
  display: inline-block;
}

.circle {
  z-index: 10;
  border: 2px solid #00000040;
  border-radius: 50%;
  justify-content: center;
  align-items: flex-start;
  width: 300px;
  height: 300px;
  display: flex;
  position: absolute;
}

.circle i {
  background: var(--clr);
  opacity: .75;
  transform-origin: bottom;
  width: 6px;
  height: 50%;
  position: absolute;
  transform: scaleY(.5);
}

.circle:first-child i {
  width: 2px;
}

.circle:nth-child(2) i {
  width: 6px;
}

.circle2 {
  z-index: 9;
  width: 240px;
  height: 240px;
}

.circle3 {
  z-index: 8;
  width: 180px;
  height: 180px;
}

.circle:before {
  content: "";
  background: var(--clr);
  box-shadow: 0 0 20px var(--clr);
  border-radius: 50%;
  width: 15px;
  height: 15px;
  position: absolute;
  top: -8px;
}

#time {
  border: 2px solid #00000080;
  border-radius: 40px;
  margin-bottom: 40px;
  padding: 10px 20px;
  font-size: 2em;
  font-weight: 600;
  display: flex;
  box-shadow: 5px 5px 10px #00000080, inset 5px 5px 20px #fff3, inset -5px -5px 15px #000000bf;
}

#time div {
  text-align: center;
  color: var(--clr);
  width: 60px;
  font-weight: 500;
  position: relative;
}

#time div:first-child:after, #time div:nth-child(2):after {
  content: ":";
  position: absolute;
  right: -4px;
}

#time div:last-child {
  color: #fff;
  justify-content: center;
  align-items: center;
  font-size: .5em;
  display: flex;
}

#time div:nth-child(2):after {
  animation: 1s step-end infinite animate;
}

@keyframes animate {
  0% {
    opacity: 1;
  }

  50% {
    opacity: .5;
  }
}

